.icon {
  display: flex;
  height: 25px;

  &__item {
    display: block;
    transition: all 0.3s ease-in-out;

    &:hover {
      transform: scale(1.2);
    }

    &--fb {
      margin-right: 25px;
    }
  }

  &__image {
    height: 24px;
    width: 24px;
  }

  &__rectangle {
    height: 10px;
    align-self: center;
  }
}
