.menu {
  left: 0;
  top: 0;
  width: 100%;
  background-color: lightblue;
  z-index: 999;
  overflow-y: auto;

  &__content {
    position: relative;
    padding: 9px 0;
    height: 100vh;

    @include onTablet {
      padding: 15px 0;
    }

    @include onDesktop {
      padding: 15px 0;
    }
  }

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    color: black;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;

    transition: border-color 0.5s;

    &:hover {
      border-color: black;
    }
  }

  &__call {
    display: block;
    width: max-content;

    text-decoration: none;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: black;
  }
}
