.nav {
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;

  &__item {
    transition: all 0.3s;

    &:hover {
      color: lightblue;
    }
  }

  &__galery {
    display: block;
    margin-bottom: 20px;
    color: #0f0e08;
  }

  &__events {
    display: block;
    margin-bottom: 80px;
    color: #0f0e08;
  }

  &__aboutUs {
    display: block;
    margin-bottom: 20px;
    color: #0f0e08;
  }

  &__contacts {
    display: block;
    margin-bottom: 80px;
    color: #0f0e08;
  }
}
