.subscription {
  height: 386px;
  background-image: url(../images/sibscribesmall.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include onTablet {
    height: 380px;
    background-image: url(../images/sibscribemidle.jpg);
  }

  @include onDesktop {
    height: 550px;
    background-image: url(../images/sibscribelarge.jpg);
  }

  &__title {
    padding: 88px 20px 20px;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: #fff;

    @include onTablet {
      padding: 82px 0 16px;
    }

    @include onDesktop {
      padding: 163px 0 16px;
    }
  }

  &__text {
    margin-bottom: 50px;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    color: #fff;
  }

  @include onTablet {
    margin-bottom: 64px;
  }

  @include onDesktop {
    margin-bottom: 72px;
  }
}
