.footer {

  &__content {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;

    @include onTablet {
      // grid-template-rows: repeat(7, 1fr);
      grid-template-columns: repeat(6, 1fr);
      column-gap: 20px;
    }

    @include onDesktop {
      grid-template-columns: repeat(12, 1fr);
      column-gap: 30px;
    }
  }

  &__logo {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    margin-bottom: 40px;
  }

  &__icon {
    grid-column: 2 / 3;
    justify-self: end;

    @include onTablet {
      grid-column: 1 / 2;
      grid-row: 1 / 2;
      justify-self: start;
      align-self: center;
    }
  }

  &__schedule {
    grid-column: 1 / 2;
    margin-bottom: 70px;

    @include onTablet {
      grid-column: 2 / 4;
      grid-row: 1 / 2;
    }

    @include onDesktop {
      grid-column: 3 / 5;
    }

  }

  &__contact {
    grid-column: 2 /3;

    @include onTablet {
      grid-column: 4 / 6;
      grid-row: 1 / 2;
    }

    @include onDesktop {
      grid-column: 5 / 7;
    }
  }

  &__line {
    grid-column: 1 / 3;
    height: 1px;
    margin-bottom: 40px;
    background-color: rgba(26, 90, 76, 0.5);

    @include onTablet {
      grid-column: 2 /6;
    }

    @include onDesktop {
      grid-column: 7 / 8;
      grid-row: 1 / 2;
      height: calc(100% - 60px);
      width: 1px;
      justify-self: center;
    }
  }

  &__nav1 {
    grid-column: 1 / 2;

    @include onTablet {
      grid-column: 2 / 3;
    }

    @include onDesktop {
      grid-column: 8 / 10;
    }
  }

  &__nav2 {
    grid-column: 2 / 3;

    @include onTablet {
      grid-column: 4 / 6;
    }

    @include onDesktop {
      grid-column: 10 / 12;
    }
  }

  &__designBy {
    grid-column: 1 / 3;
    margin-bottom: 10px;
    font-weight: 300;
    font-size: 12px;
    line-height: 140.62%;
    text-align: center;
    color: #0f0e08;

    @include onTablet {
      grid-column: 5 / 7;
    }

    @include onDesktop {
      grid-column: 11 / 13;
      justify-self: end;
    }
  }

  &__copyright {
    order: 1;
    grid-column: 1 / 3;
    margin-bottom: 50px;
    font-weight: 300;
    font-size: 12px;
    line-height: 140.62%;
    text-align: center;
    color: #0f0e08;

    @include onTablet {
      order: 0;
      grid-column: 1 / 3;
    }

    @include onDesktop {
      order: 0;
      grid-column: 1 / 4;
      justify-self: start;
    }
  }

  &__linkStart {
    position: absolute;
    display: block;
    height: 50px;
    width: 50px;
    border: 1px solid #1a5a4c;
    border-radius: 50%;
    background-image: url(../images/Vector.svg);
    background-repeat: no-repeat;
    background-position: center;
    right: 0;
    bottom: 280px;
    transition: all 0.3s;

    &:hover {
      background-color: lightblue;
      border: 1px solid #e5e5e5;
    }

    @include onTablet {
      visibility: hidden;
    }

    @include onDesktop {
      visibility: hidden;
    }
  }
}
