.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;

  &__content {
    position: relative;
    padding: 9px 0;

    @include onTablet {
      padding: 15px 0;
    }

    @include onDesktop {
      padding: 15px 0;
    }
  }

  &__menu {
    position: absolute;
    left: 0;
    top: 33px;
    height: 12px;
    width: 18px;

    @include onTablet {
      position: absolute;
      left: 0;
      top: 40px;
    }

    @include onDesktop {
      position: absolute;
      left: 0;
      top: 40px;
    }
  }

  &__logo-wrapper {
    display: flex;
    justify-content: center;

    @include onTablet {
      display: block;
      padding-left: calc(50% - 20px + 110px);
    }
  }

  &__logo {
    height: 61px;
    width: 55px;
  }
}
