.events {

  &__title {
    height: 72px;
    width: 100%;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: #0f0e08;

    @include onTablet {
      margin-bottom: 48px;
      text-align: left;
      font-size: 48px;
      line-height: 100%;
      color: #0f0e08;
    }

    @include onDesktop {
      margin-bottom: 56px;
      font-size: 48px;
    }
  }
}

.event {
  @include onDesktop {
    display: flex;
    align-items: center;
  }

  &__img {
    width: 100%;
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 44px;
    }

    @include onDesktop {
      height: 520px;
      width: 670px;
      margin-bottom: 104px;
    }
  }

  &--desktop {
    @include onDesktop {
      margin-left: 130px;
    }
  }

  &__typeDate {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include onTablet {
      margin-bottom: 32px;
      justify-content: start;
      gap: 30px;
    }
  }

  &__type {
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: #687480;

    @include onTablet {
      font-size: 14px;
    }
  }

  &__date {
    text-align: center;
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: #1a5a4c;

    @include onTablet {
      font-size: 14px;
    }
  }

  &__title {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 28px;
    line-height: 100%;
    color: #0f0e08;

    @include onTablet {
      margin-bottom: 15px;
      font-size: 36px;
    }

    &:after {
      display: inline-block;
      margin-bottom: 2px;
      content: "";
      height: 10px;
      width: 10px;
      background-color: #f14a27;

      @include onTablet {
        height: 15px;
        width: 15px;
      }
    }
  }

  &__text {
    margin-bottom: 60px;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    color: #4e4e4e;

    @include onTablet {
      margin-bottom: 64px;
      margin-right: calc(100% * 0.4);

      @include onDesktop {
        width: 100%;
      }

      &--tablet {
        margin-bottom: 112px;
      }
    }
  }

  &--lecture {
    .event {

      &__img {
        position: relative;
        width: calc(100% + 40px);
        left: -20px;

        @include onTablet {
          width: 100%;
          left: 0;
        }

        @include onDesktop {
          width: calc(100% + 110px);
          left: -55px;
        }
      }

      &__typeWrapper {
        position: relative;
        top: -70px;
        padding: 20px 0;
        background-color: #fff;
        box-shadow: 0 2px 0 rgba(26, 90, 76, 0.25);

        @include onTablet {
          width: calc(100% * 0.8);
          margin: 0 auto;
          padding: 45px 55px;
          top: -150px;
        }

        @include onDesktop {
          width: calc(100% / 2);
        }
      }

      &__typeDate {
        justify-content: center;
        gap: 20px;

        @include onTablet {
          justify-content: space-between;
        }
      }

      &__date {
        width: 220px;
        @include onTablet {
          justify-content: space-between;
        }
      }

      &__title {
        text-align: center;

        &::after {
          display: none;
        }

        @include onTablet {
          text-align: start;
        }
      }
    }
  }
}
