.form {
  display: flex;
  justify-content: center;

  &__mail {
    width: calc(100% - 50px - 40px);
    padding-left: 24px;

    @include onTablet {
      width: 470px;
      margin: 0 8px;
    }

    @include onDesktop {
      width: 470px;
      margin: 0 15px;
    }

    &::placeholder {
      font-size: 14px;
      line-height: 100%;
      color: #687480;
    }
  }

  &__img--red {
    width: 50px;
    height: 50px;
    border: 0;
    background: #f14a27;
    background-image: url(../images/errawright.svg);
    background-position: center;
    background-repeat: no-repeat;
    transition: all 0.3s;

    &:hover {
      opacity: 0.5;
      cursor: pointer;
    }

    @include onTablet {
      margin: 0 8px;
    }

    @include onDesktop {
      margin: 0 15px;
    }
  }
}
