.hero {
  @include onTablet {
    display: flex;
    flex-direction: row-reverse;
  }

  @include onDesktop {
    display: flex;
    flex-direction: row-reverse;
  }

  &__image {
    img {
      margin: 0 -20px 20px;
      width: calc(100% + 40px);
    }

    @include onTablet {
      margin: 0 -34px 34px 34px;
      width: calc((374 / 768) * 100%);
    }

    @include onDesktop {
      width: calc((725 / 1280) * 100%);
    }
  }

  &__content {
    position: relative;
    // padding: 20px 20px 0;

    @include onTablet {
      padding: 0;
      width: 340px;
      margin: 127px 20px 0 0;
    }

    @include onDesktop {
      padding: 0;
      width: 470px;
      margin: 206px 30px 0 0;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-size: 45px;
    line-height: 100%;
    text-align: center;
    color: #000;

    @include onTablet {
      margin-bottom: 38px;
      text-align: left;
      font-size: 54px;
    }

    @include onDesktop {
      margin-bottom: 89px;
      text-align: left;
      font-size: 72px;
    }
  }

  &__events {
    position: absolute;
    left: 12px;
    top: calc(100% - 90px);
    border-radius: 6px;
    display: flex;
    visibility: hidden;
    transform: rotate(-90deg);
    transform-origin: bottom left;
    transition: all 300ms;

    &--content {
      font-weight: 700;
      font-size: 14px;
      line-height: 100%;
      color: #687480;
    }

    &--line {
      height: 1px;
      width: 70px;
      margin: 7px 20px 0 0 ;
      background-color: #1a5a4c;
    }

    &:hover {
      border: 1px solid rgba(0,0,0,0.25);
      box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    }

    @include onDesktop {
      visibility: visible;
    }
  }
}
