.btn {
  display: flex;
  border: 1px solid #1a5a4c;

  @include onTablet {
    width: 270px;
  }

  @include onDesktop {
    width: 270px;
    margin-left: calc(100% - 270px);
  }

  &__text {
    display: flex;
    width: calc(100% - 50px);
    justify-content: center;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    color: #0f0e08;
    transition: all 300ms;

    @include onTablet {
      width: calc(100% - 70px);
    }

    @include onDesktop {
      width: calc(100% - 70px);
    }

    &:hover {
      background-color: #1a5a4c;
      color: #eee;
    }
  }

  &__img {
    height: 50px;
    width: 50px;
    background-color: #1a5a4c;
    background-image: url(../images/errawright.svg);
    background-position: center;
    background-repeat: no-repeat;

    @include onTablet {
      height: 70px;
      width: 70px;
    }

    @include onDesktop {
      height: 70px;
      width: 70px;
    }
  }
}
