.gallery {
  &__title {
    height: 36px;
    width: 100%;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: #0f0e08;

    @include onTablet {
      text-align: left;
    }
  }

  &__photos {
    position: relative;
    margin-bottom: 20px;

    @include onTablet {
      display: flex;
      gap: 20px;
    }

    @include onDesktop {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }

    &--1 {
      width: 100%;
      margin-right: 16px;

      @include onTablet {
        width: 50%;
        margin-right: 0;
      }

      @include onDesktop {
        width: 30%;
      }
    }

    &--1a {
      position: absolute;
      width: 100%;
      top: 0;
      left: calc(100% + 20px - 4px);

      @include onTablet {
        display: none;
      }
    }

    &--2 {
      display: none;

      @include onTablet {
        display: block;
        width: 50%;
      }

      @include onDesktop {
        display: none;
      }
    }

    &--2a {
      display: none;

      @include onTablet {
        display: block;
        position: absolute;
        width: 50%;
        left: calc(100% + 34px - 14px);
      }

      @include onDesktop {
        display: none;
      }
    }

    &--3 {
      display: none;

      @include onTablet {
        display: none;
      }

      @include onDesktop {
        display: block;
      }
    }

    &--4 {
      display: none;

      @include onTablet {
        display: none;
      }

      @include onDesktop {
        display: block;
      }
    }

    &--5 {
      display: none;

      @include onTablet {
        display: none;
      }

      @include onDesktop {
        display: block;
        width: 30%;
      }
    }
  }

  &__boxCircle {
    display: flex;
    justify-content: center;
    gap: 22px;

    &--1 {
      height: 9px;
      width: 9px;
      background-color: #1a5a4c;
      border-radius: 50%;
    }

    &--2 {
      height: 9px;
      width: 9px;
      border-color: #e0e0e0;
      background-color: #e0e0e0;
      border-radius: 50%;
    }

    &--3 {
      height: 9px;
      width: 9px;
      background-color: #e0e0e0;
      border-radius: 50%;
    }

    &--4 {
      height: 9px;
      width: 9px;
      background-color: #e0e0e0;
      border-radius: 50%;
    }
  }
}
